<template>
    <div class="tech-dmrct">
        <div class="select-program-line">
            <span class="select-program-text">{{ $t('dbgdmrct.programselect') }}</span>
            <NewSelect :isScroll="true" :module="module" :isAllowChng="!isBusy" :width="15" :height="2.4"
                :realVal="defaultPset" :options="pros" :isReturnKey="false" class="select" @valChng="valChng($event)">
            </NewSelect>
        </div>
        <div>
            <span class="is-calibration">{{ $t('dbgdmrct.isCalibration') }}</span>
            <LeetxSwitch :module="module" :value="isCalibrated" :activeVal="true" :staticVal="false" :height="2"
                class="is-calibration-btn" v-model="isCalibrated"></LeetxSwitch>
        </div>
        <div class="serial-line">
            <span class="serial-text">{{ $t('dbgdmrct.serialnumber') }}</span>
            <Counter :cnum="nthGroup" :_min="0" :_max="maxGroup" _height="2.5rem" @update='nthGroup = $event'
                class="serial-counter"></Counter>
            <span class="device-text">{{ $t('dbgdmrct.torquevalue') }}</span>
            <span class="device-input">{{ devVal }}</span>
            <span class="demarcate-val">{{ $t('dbgdmrct.referencevalue') }}</span>
            <InputVal :module="module" :sensorValue="calibrationVal" :editable="editable" class="demarcate-input"
                width="12.5vw" height="3.125vh" @updateData="updateData($event)"></InputVal>
            <LeetxButton class="add" :module="module" bgcColor="transparent" width="2rem" height="2rem" borderRadius="0"
                :useIcon="true" icon="AddDmrct" iconSize="2.5rem" @click="addToArr"></LeetxButton>
            <LeetxButton :module="module" class='del-icon' bgcColor="transparent" width="2rem" height="2rem" :useIcon="true"
                icon="DeleteAll" iconSize="2.5rem" @click="delFromArrAll"></LeetxButton>

        </div>
        <div class="mask-box">
            <tr>
                <td>{{ $t('dbgdmrct.serialnumber') }}</td>
                <td>{{ $t('dbgdmrct.torquevalue') }}</td>
                <td>{{ $t('dbgdmrct.referencevalue') }}</td>
                <td class="delBox"></td>
            </tr>
            <div class="table-box">
                <table class="demarcate-table">
                    <tr v-for="i of inputs.length" :key="i">
                        <td>{{ i }}</td>
                        <td>{{ devs[i - 1] }}</td>
                        <td>{{ inputs[i - 1] }}</td>
                        <td class="delBox">
                            <LeetxButton class="delOne" :module="module" bgcColor="transparent" width="2rem" height="2rem"
                                borderRadius="0" :useIcon="true" icon="DelDmrct" iconSize="2rem" @click="delFromArr(i)">
                            </LeetxButton>
                        </td>

                    </tr>
                </table>
            </div>

        </div>
        <div class="cacl-line">
            <LeetxButton :module="module" class="btn" textColor="#fff" borderRadius="4px" bgcColor="#00c155" width="17.25vw"
                height="3.9063vh" fontSize="1.625rem" fontFamily="--SHSC-M" :text="$t('dbgdmrct.calculate')"
                @click="calculate"></LeetxButton>
            <span class="calc-res">{{ $t('dbgdmrct.techSensitivity') }}：{{ k }}</span>
        </div>
        <LeetxButton class="confirm-box" :module="module" textColor="#fff" borderRadius="4px" bgcColor="#00c155"
            width="30.125rem" height="3.75rem" fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('dbgdmrct.save')"
            @click="submitCalibrateData"></LeetxButton>

    </div>
</template>
<script>
import InputVal from '../input/InputVal.vue'
import Counter from '../controls/Counter.vue'
import NewSelect from '../select/NewSelect.vue'
import { devCalibrateExitApi, devCalibrateProApi, devCalibrateSensorKb, devCalibrateSensorResult, devCalibrateSensorSet, devCalibrateEnableApi } from '../../api/debug/debug'
import { messageApi } from '../message/message'
import { curPfListApi, pfCurSelApi } from '../../api/profile/profile'
// import { pdataResultTighten } from '../../api/history/history'
// import average from '../../utils/formula.js'
import LeetxButton from '../controls/LeetxButton.vue'
import cnfrmWin from '@/dialogs/common/confirm/cnfrm'
import langvue from '@/plugins/langVue.js'
export default {
    components: {
        NewSelect,
        LeetxButton,
        Counter,
        InputVal,
    },
    data() {
        return {
            calibrateData: {},
            gain: 0,
            offset: 0,
            devs: [],
            inputs: [],
            files: [],
            pros: {},
            res: { ret_sensor: 0.001, ret_pos: 0.002 },
            nthGroup: 0,
            k: 1,
            b: 0,
            module: "CALIBRATION",
            times: 0,
            devVal: 0,
            calibrationVal: 0,
            editable: false,
            isCalibrated: false,
            global_k: '',
            global_isCalibrated: ''
        }
    },
    methods: {
        getRes() {
            devCalibrateSensorKb().then((result) => {
                if (result.status == 0) {
                    this.k = result.data.calib_k
                    this.b = result.data.calib_offset
                } else {
                    messageApi.error([this.$t('dbgdmrct.failedGetCalibrationData')])
                }
            }).catch(() => {
                messageApi.error([this.$t('dbgdmrct.failedGetCalibrationData')])
            });
        },
        enterProPage() {
            // ("进入工艺标定");
            devCalibrateProApi().then((result) => {
                if (result.status != 0) {
                    messageApi.warning([this.$t('dbgdmrct.entryCalibrationFail')])
                } else {
                    devCalibrateEnableApi().then((result) => {
                        this.isCalibrated = result.data.calibrate_enable
                        this.k = result.data.calibrate_k
                        this.global_isCalibrated = JSON.stringify(this.isCalibrated)
                        this.global_k = JSON.stringify(this.k)
                    }).catch(() => {
                        messageApi.warning([this.$t('dbgdmrct.enableProcessCalibrationFail')])
                    });
                }
            }).catch(() => {
                messageApi.warning([this.$t('dbgdmrct.entryCalibrationFail')])
            });
        },
        leaveProPage() {
            devCalibrateExitApi().then((result) => {
                if (result.status != 0) {
                    messageApi.warning([this.$t('dbgdmrct.exitFail')])
                }
            }).catch(() => {
                messageApi.warning([this.$t('dbgdmrct.exitFail')])
            });
        },
        getAllProfiles() {
            curPfListApi().then((result) => {
                if (result.status == 0) {
                    this.files = result.data
                    this.pfList = this.files.sort(this.sortBy('pset'))
                    for (let i = 0; i < this.files.length; i++) {
                        this.pros[`${this.files[i].pset} [${this.files[i].name}]`] = this.files[i].pset
                    }
                    // this.defaultPset= this.$store.state.g_status.pset
                }
            })
        },
        valChng(pset) {
            if (this.$store.state.g_status.psetsrc != 0x01) {
                let msgOne = this.$t('promsg.msgOne')
                let msgTwo = this.$t('promsg.msgTwo')
                messageApi.warning([msgOne, msgTwo])
                return
            }
            // let pset=this.pros[name]
            // this.res.ret_sensor=parseFloat(Math.random().toFixed(3))
            if (pset != undefined) {
                pfCurSelApi(pset).then(() => {
                    messageApi.success([this.$t('promsg.activate_suc')])
                    devCalibrateEnableApi().then((result) => {
                        this.isCalibrated = result.data.calibrate_enable
                        this.global_isCalibrated = JSON.stringify(this.isCalibrated)
                        this.k = result.data.calibrate_k
                        if (result.status != 0) {
                            messageApi.warning([this.$t('dbgdmrct.enableProcessCalibrationFail')])
                        }
                    }).catch(() => {
                        messageApi.warning([this.$t('dbgdmrct.enableProcessCalibrationFail')])
                    });
                }).catch(() => {
                    messageApi.error([this.$t('promsg.activate_fail')])
                });
            }

        },
        clbck() {
            messageApi.warning([this.$t('promsg.run_status'), this.$t('promsg.nactivate_file')])
            return
        },
        // 排序
        sortBy(field) {
            return function (a, b) {
                return a[field] - b[field];
            }
        },
        updateData(val) {
            this.calibrationVal = val.sensorValue
        },
        addToArr() {
            let dev = parseFloat(this.devVal.toFixed(3))
            let input = parseFloat(this.calibrationVal.toFixed(3))
            if (dev == '0') return messageApi.warning([this.$t('dbgdmrct.notBeZero')])
            this.devs.splice(this.nthGroup, 0, dev)
            this.inputs.splice(this.nthGroup, 0, input)
            this.nthGroup += 1
        },
        delFromArr(i) {
            if (this.nthGroup == 0) { return }
            this.devs.splice(i - 1, 1)
            this.inputs.splice(i - 1, 1)
            this.nthGroup = this.nthGroup > (this.inputs.length - 1) ? this.inputs.length : this.nthGroup
        },
        delFromArrAll() {
            if (this.devs.length == 0 && this.inputs.length == 0) { return }
            let title = this.$t('dbgdmrct.deleteConfigurate')
            let confirmText = this.$t('dbgdmrct.yes')
            let cancelText = this.$t('dbgdmrct.no')
            let msg = this.$t('dbgdmrct.deleteAll')
            cnfrmWin({ title, confirmText, cancelText, msg }).then(() => {
                this.devs = [],
                    this.inputs = [],
                    this.nthGroup = 0
            })
        },
        calculate() {
            //获取接口k，b数据
            devCalibrateSensorKb().then((result) => {
                if (result.status == 0) {
                    this.k = result.data.calib_k
                    this.b = result.data.calib_offset
                    if (this.devs.length != this.inputs.length || this.devs.length == 0) return
                    let c = 0
                    for (let i = 0; i < this.inputs.length; i++) {
                        let y = this.inputs[i] - this.b
                        let x = this.devs[i] - this.b
                        let k = y / x
                        c += k / this.inputs.length
                    }
                    this.k = parseFloat(c).toFixed(5)
                }
            }).catch(() => {
                messageApi.error([this.$t('dbgdmrct.failRead')])
            })

        },
        // 写传感器值
        submitCalibrateData() {
            const calib_k = this.k
            const calib_offset = this.b
            const calibrate_enable = this.isCalibrated
            this.global_k = JSON.stringify(calib_k)
            this.global_isCalibrated = JSON.stringify(this.isCalibrated)
            devCalibrateSensorSet({
                calib_k,
                calib_offset,
                calibrate_enable
            }).then((result) => {
                if (result.status == 0) {
                    let msg = langvue.global.t('global.success')
                    messageApi.success([msg])
                } else {
                    messageApi.error([this.$t('dbgdmrct.setupFail')])
                }
            }).catch(() => {
                messageApi.error([this.$t('dbgdmrct.setupFail')])
            });
        },
        getDevVal() {
            devCalibrateSensorResult().then((result) => {
                // this.devVal = 1
                this.devVal = parseFloat(result.data.inner_maxval.toFixed(3))
                if (result.data.has_ext) {
                    this.calibrationVal = result.data.ext_maxval
                } else {
                    this.editable = true
                }
            }
            )


        }
    },
    beforeRouteLeave(to, from, next) {
        let global_k_unchange = this.global_k == JSON.stringify(this.k)
        let global_isCalibrated_unchange = this.global_isCalibrated == JSON.stringify(this.isCalibrated)
        if (global_k_unchange && global_isCalibrated_unchange) {
            next()
        } else {
            let title = this.$t('setcontrol.saveSet')
            let confirmText = this.$t('setcontrol.yes')
            let cancelText = this.$t('setcontrol.no')
            let msg = this.$t('setcontrol.isSaveSet')
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                this.submitCalibrateData()
                next()
            }).catch(() => {
                next()
            });
        }
    },
    mounted() {
        if (this.enable)
            this.enterProPage()
        // this.getRes()
        this.getDevVal()
    },
    unmounted() {
        if (this.enable)
            this.leaveProPage()
    },
    created() {
        this.global_k = JSON.stringify(this.k)
        this.global_isCalibrated = JSON.stringify(this.isCalibrated)
        this.getAllProfiles()
    },
    computed: {
        isBusy() {
            return this.$store.state.g_status.busy
            // return true
        },
        runOk() {
            return this.$store.state.g_status["rok"];
        },
        torque() {
            return parseFloat((this.res.ret_sensor).toFixed(3))
            // return ""
        },
        maxGroup() {
            return this.inputs.length
        },
        defaultPset() {
            return this.$store.state.g_status["pset"];
        },
        enable() {
            return this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4;
        }
    },
    watch: {
        runOk: {
            // deep: true,
            handler() {
                if (this.runOk && this.enable) {
                    this.getDevVal()
                }
            }
        },
    },
}
</script>
<style scoped>
.tech-dmrct {
    position: relative;
    height: 100%;
    width: 100%;
}


.select-program-line,
.serial-line {
    top: 1rem;
    position: relative;
    height: 3.9063vh;
    width: 100%;

}

.serial-line {
    top: 2rem;
}

.select-program-text,
.serial-text,
.is-calibration {
    position: absolute;
    left: 4.875vw;
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font1_625);
    color: var(--C3B);

}

.is-calibration {
    top: 2.6rem;
    right: -15.8rem;
}

.is-calibration-btn {
    position: absolute;
    top: 1.6rem;
    right: 1.9rem;
}

.delete-all {
    position: absolute;
    left: 75.125vw;
    top: 2%;
    /* transform: translateY(-50%); */
    font-size: var(--font1_625);
    color: var(--C3B);
}

.del-icon {
    position: absolute;
    left: 92.125vw;
    top: 14%;
}

.select {
    position: absolute;
    top: 0.4vh;
    left: calc(8rem + 4.875vw);
}

.btn {
    position: absolute;
    left: 4.875vw;
    top: 60.0625vh;
    transform: translateY(-50%);
}

.serial-counter {
    position: absolute;
    left: 13.2825vw;
    top: 50%;
    transform: translateY(-50%);
}

.device-text {
    position: absolute;
    left: 30.25vw;
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font1_625);
    color: var(--C3B);
}

.device-input {
    display: inline-block;
    width: 78px;
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 42.8vw;
    font-size: 1.625rem;
}

.demarcate-val {
    position: absolute;
    left: 54.875vw;
    top: 50%;
    transform: translateY(-50%);
    font-size: var(--font1_625);
    color: var(--C3B);
}

.demarcate-input {
    position: absolute;
    left: 68.75vw;
    top: 50%;
    transform: translateY(-50%);
}

.trash {
    position: absolute;
    left: 84.5vw;
    top: 50%;
    transform: translateY(-50%);
}

.add {
    position: absolute;
    top: 14%;
    right: 11vw;
}

.mask-box {
    position: relative;
    width: 95vw;
    left: 50%;
    transform: translateX(-50%);
    top: 3rem;
    height: 39.063vh;
    overflow: hidden;
    border-bottom: 2px solid var(--C00);
}

.table-box {
    position: relative;
    left: 0;
    top: 0;
    width: calc(95vw + 18px);
    overflow-y: scroll;
    height: 35.1567vh;


}

.demarcate-table {
    position: absolute;
    width: 95vw;
    left: 0;
    top: 0;
}

tr {
    height: 3.9063vh;
    line-height: 3.9063vh;
    display: flex;
    box-sizing: border-box;
    border-bottom: 1px solid var(--CD4);
}

tr:first-child {
    border-top: 2px solid var(--C00);
    border-bottom: 1px solid var(--C00);
}

.demarcate-table tr:first-child {
    border-top: none;
    border-bottom: 1px solid var(--CD4);

}

tr:last-child {
